@import url('https://fonts.googleapis.com/css?family=Bubblegum+Sans&display=swap');
* {
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  background: url('./yinji.jpg') no-repeat;
  background-size: 100% 100%;
  background-color: #F44436;
  position: relative;
}

.m-mask {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1005;
  background: rgba(0, 0, 0, 0.2);
}

.m-guide {
  width: 94vw;
  height: 60px;
  background: rgb(255, 255, 255);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  position: absolute;
  padding-top: 20px;
  top: -1px;
  left: 50%;
  transform: translateX(-50%);
  // text-align: left;
  .guideImg {
    position: absolute;
    width: 82px;
    height: 50px;
    z-index: 1006;
    right: 5px;
    top: 5px;
    background: url(./guide.png) no-repeat;
    background-size: contain;
  }
}

.btn {
  background: #F44436;
  border-radius: 20px;
  width: 60%;
  height: 40px;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  box-shadow: 0px 0px 10px white;
  text-align: center;
  line-height: 40px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  z-index: 1001;
}

.bg-bubbles {
  position: absolute;
  // 使气泡背景充满整个屏幕；
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  // 如果元素内容超出给定的宽度和高度，overflow 属性可以确定是否显示滚动条等行为；
  overflow: hidden;
  z-index: 100;
  li {
    position: absolute;
    // bottom 的设置是为了营造出气泡从页面底部冒出的效果；
    bottom: -160px;
    // 默认的气泡大小；
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba(250, 246, 246, 0.5);
    list-style: none;
    // 使用自定义动画使气泡渐现、上升和翻滚；
    animation: square 8s infinite;
    transition-timing-function: linear;
    // 分别设置每个气泡不同的位置、大小、透明度和速度，以显得有层次感；
    &:nth-child(1) {
      left: 10%;
    }
    &:nth-child(2) {
      left: 20%;
      width: 90px;
      height: 90px;
      animation-delay: 2s;
      animation-duration: 7s;
    }
    &:nth-child(3) {
      left: 25%;
      animation-delay: 4s;
    }
    &:nth-child(4) {
      left: 40%;
      width: 60px;
      height: 60px;
      animation-duration: 8s;
      background-color: rgba(255, 255, 255, 0.6);
    }
    &:nth-child(5) {
      left: 70%;
    }
    &:nth-child(6) {
      left: 80%;
      width: 120px;
      height: 120px;
      animation-delay: 3s;
      background-color: rgba(255, 255, 255, 0.7);
    }
    &:nth-child(7) {
      left: 32%;
      width: 160px;
      height: 160px;
      animation-delay: 2s;
    }
    &:nth-child(8) {
      left: 55%;
      width: 20px;
      height: 20px;
      animation-delay: 4s;
      animation-duration: 15s;
    }
    &:nth-child(9) {
      left: 25%;
      width: 10px;
      height: 10px;
      animation-delay: 2s;
      animation-duration: 12s;
      background-color: rgba(131, 3, 3, 0.3);
    }
    &:nth-child(10) {
      left: 15%;
      width: 160px;
      height: 160px;
      animation-delay: 5s;
    }
  }
  // 自定义 square 动画；
  @keyframes square {
    0% {
      opacity: 0.6;
      transform: translateY(0px) 
    }
    // 25% {
    //   opacity: 0.75;
    //   transform: translateY(-400px) rotate(90deg)
    // }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      transform: translateY(-1000px) ;
    }
  }
}

